<template>
	<div class="container">
		<div class="top-content">
			<div class="left-content">
				
				<span class="tip-text">{{currentIndex+1}}</span> 
				<span class="tip-text">{{dataList.length - currentIndex-1}}</span> 
				<span class="tip-text">{{dataList.length}}</span> 
			</div>
			<div class="left-content">
				{{paper.name}} 
				<span class="tip-text">{{dataList.length}}</span> 
			</div>
			<div class="right-content">
				
				<span class="tip-text">{{SecondToDate}}</span>
			</div>
			<div class="right-content">
				<el-button type="danger" round @click="leaveTap"></el-button>
			</div>
		</div>
		<div class="detail-content" v-if="!isEndFlag">
			<div class="item-content">
				<span class="label">
					({{ruleForm.score}})
				</span>
				<span class="content">{{ruleForm.questionname}}</span>
				<el-tag type="success" v-if="ruleForm.type==0"></el-tag>
				<el-tag type="warning" v-if="ruleForm.type==1"></el-tag>
				<el-tag type="info" v-if="ruleForm.type==2"></el-tag>
				<el-tag type="primary" v-if="ruleForm.type==3"></el-tag>
				<el-tag type="danger" v-if="ruleForm.type==4"></el-tag>
			</div>
			<div
				v-if="(ruleForm.type==0||ruleForm.type==2)&&!isSubmitFlag"
				class="item-content"
				style="border-top:1px solid #eeeeee"
				>
				<span class="label"></span>
				<span class="content">
					<el-select v-model="answer">
						<el-option
							:label="item.text"
							:value="item.code"
							v-for="(item,index) in options"
							v-bind:key="index"
						></el-option>
					</el-select>
				</span>
			</div>
			<div
				v-if="ruleForm.type==3&&!isSubmitFlag"
				class="item-content"
				style="border-top:1px solid #eeeeee"
				>
				<span class="label"></span>
				<span class="content">
					<el-input v-model="answer" style="width:300px" placeholder="" clearable></el-input>
				</span>
			</div>
			<div
				v-if="ruleForm.type==4&&!isSubmitFlag"
				class="item-content"
				style="border-top:1px solid #eeeeee"
				>
				<span class="label"></span>
				<span class="content">
					<el-input v-model="answer" type="textarea" style="width:300px" placeholder="" clearable></el-input>
				</span>
			</div>
			<div
				v-if="ruleForm.type==1&&!isSubmitFlag"
				class="item-content"
				style="border-top:1px solid #eeeeee"
				>
				<span class="label"></span>
				<span class="content">
					<el-select v-model="answer" multiple>
						<el-option
							:label="item.text"
							:value="item.code"
							v-for="(item,index) in options"
							v-bind:key="index"
							></el-option>
					</el-select>
				</span>
			</div>
			<div v-if="isSubmitFlag" class="item-content" style="border-top:1px solid #eeeeee">
				<span class="label"></span>
				<span class="content">{{answer}}</span>
			</div>
			<div v-if="isSubmitFlag" class="item-content" style="color:#888888">
				<span
					v-if="ruleForm.type!=4"
					class="label"
					style="color:red;margin-right:20px"
					>{{ answer==this.ruleForm.answer?'':''}}</span>
				<span class="label"></span>
				<span class="content">{{ruleForm.analysis}}</span>
			</div>
			<div class="item-content" style="color:#888888">
				<el-button v-if="!isSubmitFlag" @click="submitTap" type="primary"></el-button>
				<el-button v-if="isSubmitFlag" @click="nextTap" type="success"></el-button>
			</div>
		</div>
		<div class="detail-content" v-if="isEndFlag">
			<div class="item-content">
				<span class="label">
					
					<span style="font-size:24px;font-weight:blod;color:red">{{this.score}}</span>
				</span>
				<span class="content"></span>
			</div>
			<div class="item-content" style="color:#888888">
				<el-button @click="finishTap" type="primary"></el-button>
			</div>
		</div>
	</div>
</template>
<script>
	export default {
		data() {
			return {
				dataList: [],
				// 
				paper: {},
				// 
				currentIndex: 0,
				ruleForm: {},
				// 
				options: null,
				// 
				answer: "",
				// 
				score: 0,
				// 
				inter: null,
				// 
				count: 0,
				// 
				isSubmitFlag: false,
				user: {},
				// 
				isEndFlag: false,
				// 
				hassubject: false
			};
		},
		computed: {
			SecondToDate: function() {
				var time = this.count;
				if (null != time && "" != time) {
					if (time > 60 && time < 60 * 60) {
						time =
						parseInt(time / 60.0) +
						"" +
						parseInt((parseFloat(time / 60.0) - parseInt(time / 60.0)) * 60) +
						"";
					} else if (time >= 60 * 60 && time < 60 * 60 * 24) {
						time =
						parseInt(time / 3600.0) +
						"" +
						parseInt(
						  (parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						) +
						"" +
						parseInt(
						  (parseFloat(
							(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						  ) -
							parseInt(
							  (parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
							)) *
							60
						) +
						"";
					} else if (time >= 60 * 60 * 24) {
						time =
						parseInt(time / 3600.0 / 24) +
						"" +
						parseInt(
						  (parseFloat(time / 3600.0 / 24) - parseInt(time / 3600.0 / 24)) *
							24
						) +
						"" +
						parseInt(
						  (parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						) +
						"" +
						parseInt(
						  (parseFloat(
							(parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
						  ) -
							parseInt(
							  (parseFloat(time / 3600.0) - parseInt(time / 3600.0)) * 60
							)) *
							60
						) +
						"";
					} else {
						if(parseInt(time)<=0) {
							time = "0";
						} else {
							time = parseInt(time) + "";
						}
					}
				}
				return time;
			}
		},
		destroyed: function() {
			window.clearInterval(this.inter);
		},
		mounted() {
			this.$http({
				url: `${this.$storage.get("sessionTable")}/session`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.user = data.data;
				} else {
					this.$message.error(data.msg);
				}
			});
			console.log(this.$route.params.id);
			//  
			var params = {
				page: 1,
				limit: 999,
				sort: "sequence",
				paperid: this.$route.params.id
			};
			this.$http({
				url: this.$api.examquestionpage,
				method: "get",
				params: params
			}).then(({ data }) => {
				if (data && data.code === 0) {
					for(let x in data.data.list){
						if(data.data.list[x].type==4){
							this.hassubject = true
						}
						if(data.data.list[x].options){
							data.data.list[x].optionList = JSON.parse(data.data.list[x].options)
						}
					}
					this.dataList = data.data.list;
					this.ruleForm = this.dataList[this.currentIndex];
					this.options = this.ruleForm.optionList
				} else {
					this.dataList = [];
				}
				this.dataListLoading = false;
			});
			// 
			this.$http({
				url: `${this.$api.exampaperinfo}${this.$route.params.id}`,
				method: "get"
			}).then(({ data }) => {
				if (data && data.code === 0) {
					this.paper = data.data;
					if (this.paper.time) {
						this.count = this.paper.time*60;
						if (this.count > 0) {
							var _this = this;
							this.inter = window.setInterval(function() {
								_this.count = _this.count - 1;
								if (_this.count < 0) {
									window.clearInterval(_this.inter);
									_this.isEndFlag = true;
								}
							}, 1000);
						}
					}
				} else {
					this.$message.error(data.msg);
				}
			});
		},
		methods: {
			leaveTap() {
				this.$confirm(`?0`, "", {
					confirmButtonText: "",
					cancelButtonText: "",
					type: "warning"
				}).then(() => {
					this.isEndFlag = true;
				});
			},
			finishTap() {
				this.$router.go(-1);
			},
			submitTap() {
				if(!this.answer) {
					this.$message.error('')
					return;
				}
				let myscore = 0;
				if (this.ruleForm.type == 1) {
					this.answer = this.answer.sort().join(",");
				}
				this.isSubmitFlag = true;
				this.ruleForm.myanswer = this.answer
				if(this.ruleForm.type!=4){
					if (this.answer == this.ruleForm.answer) {
						this.score = Number(this.score) +Number(this.ruleForm.score) ;
						this.ruleForm.myscore = this.ruleForm.score;
					}else{
						this.ruleForm.myscore = 0
					}
				}else{
					this.ruleForm.myscore = 0
				}
				this.saverecord(this.ruleForm)
			},
			// 
			saverecord(row){
				let record = {
					userid: this.user.id,
					username: this.user.xingming,
					paperid: this.paper.id,
					papername: this.paper.name,
					questionid: row.id,
					questionname: row.questionname,
					options: row.options,
					score: row.score,
					answer: row.answer,
					analysis: row.analysis,
					myscore: row.myscore,
					
					myanswer: row.myanswer,
					type: row.type,
					ismark: this.hassubject? 0 : 1
				};
				this.$http({
					url: `${this.$api.examrecordsave}`,
					method: "post",
					data: record
				}).then(({
					data
				}) => {});
			},
			nextTap() {
				if (this.currentIndex == this.dataList.length - 1) {
					this.isEndFlag = true;
				} else {
					this.currentIndex = this.currentIndex + 1;
					this.ruleForm = this.dataList[this.currentIndex];
					this.options = JSON.parse(this.ruleForm.options);
					this.isSubmitFlag = false;
				}
				this.answer = "";
			},
		}
	};
</script>
<style lang="scss" scoped>
	.container {
		border: 1px solid #eeeeee;
	}
	
	.tip-text {
		font-size: 24px;
		color: #f96332;
	}
	
	.top-content {
		display: flex;
		justify-content: space-between;
		padding: 10px;
		font-size: 18px;
		background: #409eff;
		color: #ffffff;
		border-radius: 20px;
	}
	.detail-content {
		padding: 20px;
		color: #333333;
	
		.item-content {
			padding: 20px;
		}
	}
</style>
